@charset "UTF-8";
html {
  min-height: 100%;
  background: black; }

body {
  font-family: Raleway;
  width: 100%;
  margin: auto;
  height: 100%; }

.color--dark-blue {
  color: #00344e; }

.color--grey {
  color: #5e5e5e; }

.color--red {
  color: #dd1a31 !important; }

.transition--disable {
  transition: initial !important; }

/* хром, сафари */
body::-webkit-scrollbar {
  width: 0; }

/* ie 10+ */
body {
  -ms-overflow-style: none; }

/* фф (свойство больше не работает)*/
body {
  overflow: -moz-scrollbars-none; }

.content-wrapper {
  position: fixed;
  left: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  max-width: 1080px;
  max-height: 1920px;
  background-color: #25283d;
  overflow: hidden;
  box-sizing: border-box;
  padding: 1px;
  min-height: 1920px;
  overflow-x: hidden; }
  .content-wrapper--relative {
    max-height: initial;
    overflow: visible; }

.header {
  transition: all ease-in-out 0.5s;
  position: absolute;
  transform: translateY(-110%);
  top: 0;
  left: 0;
  color: white;
  font-size: 100px;
  line-height: 53px;
  font-weight: 900;
  width: 100%;
  background-color: #142664;
  z-index: 99; }
  .header--show {
    transform: translateY(0%); }
  .header__image {
    width: 100%; }

.slider {
  opacity: 1;
  transition: opacity ease-in-out 0.5s;
  position: absolute;
  top: 0;
  left: 0;
  transition-delay: .4s;
  width: 100%;
  height: 100%;
  overflow: hidden; }
  .slider__item-list {
    position: absolute;
    top: 0;
    width: 100%;
    text-align: center; }
  .slider__item {
    transition: all ease-in-out .8s;
    transform: translateY(0);
    text-align: left;
    margin: 5% 10% 5%;
    background-color: #e30359;
    display: inline-block;
    position: relative;
    width: 800px;
    height: 800px; }
    .slider__item--active .slider__image {
      transition: all ease-in-out 0.5s;
      transform: scale(0.8); }
  .slider__image {
    transition: all ease-in-out 0.5s;
    transform: scale(1);
    line-height: 0;
    overflow: hidden; }
    .slider__image img {
      width: 100%; }
  .slider__text-wrapper {
    margin-left: -4px;
    box-sizing: border-box;
    z-index: 1;
    width: 100%;
    color: #ffd287;
    letter-spacing: -5px;
    font-size: 90px;
    line-height: 64px;
    font-weight: 900;
    position: absolute; }
    .slider__text-wrapper--shadow {
      text-shadow: 0px 0px 33.06px rgba(0, 0, 0, 0.67); }
  .slider--hide {
    opacity: 0; }
    .slider--hide .slider__item:nth-child(1) {
      transition-delay: 0.05s;
      transform: translateY(-50%); }
    .slider--hide .slider__item:nth-child(2) {
      transition-delay: 0.1s;
      transform: translateY(-50%); }
    .slider--hide .slider__item:nth-child(3) {
      transition-delay: 0.15s;
      transform: translateY(-50%); }
    .slider--hide .slider__item:nth-child(4) {
      transition-delay: 0.2s;
      transform: translateY(-50%); }

.alert {
  transition: all ease-in-out 0.5s;
  position: fixed;
  top: 300px;
  left: 0;
  right: 0;
  margin: auto;
  max-width: 210px;
  z-index: 99;
  background-color: #ffffff;
  color: #4d4d4d;
  font-weight: 600;
  font-size: 28px;
  text-align: center;
  padding: 30px;
  opacity: 0; }
  .alert__text-wrapper {
    color: #4d4d4d; }
  .alert__image {
    margin: 20px 0px; }
  .alert--active {
    top: 240px;
    opacity: 1; }

.product {
  transition: all ease-in-out 0.5s;
  background-color: white;
  opacity: 0;
  transform: translateY(40%);
  margin: 10% auto;
  padding: 40px;
  max-width: 800px;
  box-sizing: border-box; }
  .product__image {
    margin-top: 30px; }
    .product__image img {
      width: 100%; }
  .product__title {
    font-weight: 900;
    font-size: 81px;
    line-height: 57px;
    letter-spacing: -3px; }
  .product__logo {
    margin-top: 30px;
    width: 30%; }
    .product__logo img {
      width: 100%; }
    .product__logo--right {
      float: right; }
  .product--show {
    opacity: 1;
    transform: translateY(0%); }
  .product__description {
    margin-top: 30px;
    font-size: 30px; }
  .product ul {
    list-style: none; }
  .product__video {
    margin-top: 30px;
    width: 100%; }
  .product__asc {
    margin-top: 30px;
    margin-bottom: -15%;
    text-align: center; }
    .product__asc img {
      display: inline-block; }
  .product li {
    position: relative; }
    .product li:after {
      position: absolute;
      left: -4%;
      top: 18px;
      content: " ";
      width: 10px;
      height: 10px;
      background-color: #e30359; }

.likes {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: #e30359;
  padding: 20px;
  text-align: center;
  min-width: 51px;
  right: 0; }
  .likes__wrapper {
    position: fixed;
    width: 1080px;
    height: 1920px; }
  .likes__count {
    margin-top: 10px;
    color: white;
    font-weight: 500;
    font-size: 30px; }
  .likes__icon {
    width: 50px;
    text-align: center;
    display: inline-block; }
    .likes__icon img {
      display: inline-block;
      width: 100%; }
  .likes__pop-up {
    z-index: 99;
    transition: all 0.5s ease-in-out;
    transform: translateY(-30%) translateX(-50%);
    background: url("../image/like__background.png");
    background-size: 100%;
    padding: 60px;
    opacity: 0;
    position: fixed;
    display: inline-block;
    color: white;
    font-size: 40px;
    top: 50%;
    left: 50%;
    overflow: hidden;
    text-align: center;
    font-weight: 600; }
    .likes__pop-up--show {
      opacity: 1;
      transform: translate(-50%, -50%); }
    .likes__pop-up__text {
      max-width: 400px; }
