.content-wrapper{
  position: fixed;
  left: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  max-width: 1080px;
  max-height: 1920px;
  background-color: #25283d;
  overflow: hidden;
  box-sizing: border-box;
  padding: 1px;
  min-height: 1920px;
  overflow-x: hidden;

  &--relative{
    max-height: initial;
    overflow: visible;
  }
}